import React, { useState, useRef, useEffect } from "react";
import { CSSTransition } from "react-transition-group";
import "./Reminder.scss";
import "../order/Order.scss";

function useOnClickOutside(ref, handler) {
    useEffect(
        () => {
            const listener = (event) => {
                if (!ref.current || ref.current.contains(event.target)) {
                    return;
                }
                handler(event);
            };
            document.addEventListener("mousedown", listener);
            document.addEventListener("touchstart", listener);
            return () => {
                document.removeEventListener("mousedown", listener);
                document.removeEventListener("touchstart", listener);
            };
        }, [ref, handler]
    );
}

const Reminder = () => {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const ref = useRef();
    useOnClickOutside(ref, () => setIsModalOpen(false));
    return (
        <div className="reminder-block">
            <div className="container">
                <div className="reminder-wrapper">
                    <div className="reminder__content">
                        <div className="reminder__title">–ù–ê–ü–û–ú–ò–ù–ê–ù–ò–ï</div>
                        <div className="reminder__text">–ò—â–∏—Ç–µ –±—ã—Å—Ç—Ä–æ —É—Å–ª—É–≥—É, –Ω–∞—Ö–æ–¥–∏—Ç–µ –∫–æ–Ω—Ç–∞–∫—Ç—ã –º–∞–≥–∞–∑–∏–Ω–æ–≤, –æ—Ü–µ–Ω–∏–≤–∞–π—Ç–µ —É—Å–ª—É–≥—É</div>
                    </div>       
                    <div className="btn" onClick={() => {setIsModalOpen(true)}}>–°–∫–∞—á–∞—Ç—å –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ</div>
                    <CSSTransition
                        in={isModalOpen}
                        timeout={200}
                        classNames="popup"
                        unmountOnExit
                    >
                        <div className="popup-download" ref={ref}>
                            <div className="error"></div>
                            <div className="content">
                                <div className="content__title">–°—Å—ã–ª–∫–∞ –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ —Ä–∞–±–æ—Ç–∞–µ—Ç ...</div>
                                <div className="content__text">–ë—É–¥—å—Ç–µ –ø–µ—Ä–≤—ã–º–∏ –∫—Ç–æ —É–∑–Ω–∞–µ—Ç –æ –≥–æ—Ç–æ–≤–Ω–æ—Å—Ç–∏ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è - –æ—Å—Ç–∞–≤–∏–≤ —Å–≤–æ—é –ø–æ—á—Ç—É –Ω–∏–∂–µ!</div>
                                <input className="input" placeholder="–í–∞—à–∞ –ø–æ—á—Ç–∞" type="email"></input>
                                <div className="btn">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</div>
                            </div>
                        </div>
                    </CSSTransition>
                </div>
            </div>
        </div>
    );
}

export default Reminder;