import React, { useState, useRef, useEffect } from "react";
import {CSSTransition} from "react-transition-group";
import "./Order.scss";

function useOnClickOutside(ref, handler) {
    useEffect(
        () => {
            const listener = (event) => {
                if (!ref.current || ref.current.contains(event.target)) {
                    return;
                }
                handler(event);
            };
            document.addEventListener("mousedown", listener);
            document.addEventListener("touchstart", listener);
            return () => {
                document.removeEventListener("mousedown", listener);
                document.removeEventListener("touchstart", listener);
            };
        }, [ref, handler]
    );
}


const Download = () => {
    const [isOpen, setIsOpen] = useState(false);
    const ref = useRef();
    useOnClickOutside(ref, () => setIsOpen(false));

    return (
        <div className="download-form">
            <div className="download-form__img"></div>
            <div className="download-form__content">
                <div className="download-form__title">–ò–õ–ò ...</div>
                <div className="text download-form__text">—Å–∫–∞—á–∞–π—Ç–µ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ –¥–ª—è –±—ã—Å—Ç—Ä–æ–≥–æ –¥–æ–±–∞–≤–ª–µ–Ω–∏—è —É—Å–ª—É–≥</div>
                <div className="btn" onClick={() => {setIsOpen(true)}}>–°–∫–∞—á–∞—Ç—å</div>
                <div className="add-text">–°–ø–∞—Å–∏–±–æ —á—Ç–æ –≤—ã —Å –Ω–∞–º–∏!</div>
            </div>
            <CSSTransition
                in={isOpen}
                timeout={200}
                classNames="popup"
                unmountOnExit
            >
                <div className="popup-download" ref={ref}>
                    <div className="error"></div>
                    <div className="content">
                        <div className="content__title">–°—Å—ã–ª–∫–∞ –≤—Ä–µ–º–µ–Ω–Ω–æ –Ω–µ —Ä–∞–±–æ—Ç–∞–µ—Ç ...</div>
                        <div className="content__text">–ë—É–¥—å—Ç–µ –ø–µ—Ä–≤—ã–º–∏ –∫—Ç–æ —É–∑–Ω–∞–µ—Ç –æ –≥–æ—Ç–æ–≤–Ω–æ—Å—Ç–∏ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è - –æ—Å—Ç–∞–≤–∏–≤ —Å–≤–æ—é –ø–æ—á—Ç—É –Ω–∏–∂–µ!</div>
                        <input className="input" placeholder="–í–∞—à–∞ –ø–æ—á—Ç–∞" type="email"></input>
                        <div className="btn">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</div>
                    </div>
                </div>
            </CSSTransition>
        </div>
    );
}

export default Download;